<template>
  <div class="wrapper">
    <typeChange @change="changeType"></typeChange>
    <div class="content">
      <div class="c-left">
        <ctitle :title="'信访量分布'"></ctitle>
        <div class="detail">
          <p class="allNumber">
            <span>
              <incNumber class="number" :incNumber="jctotal"></incNumber>
            </span>件次
          </p>
          <div class="top3">
            <div class="label">TOP</div>
            <ul class="list">
              <li class="item" v-for="(item,index) of jcTop3" :key="index">
                <span class="order">{{index+1}}</span>
                <span class="name">{{item.name}}</span>
                <incNumber class="number" :incNumber="item.value"></incNumber>
                <span class="unit">件次</span>
              </li>
            </ul>
          </div>
        </div>
        <Map class="map" :params="mapParams" @change="handleMapClick"></Map>
      </div>
      <div class="c-right">
        <div class="c-r-t">
          <ctitle :title="'信访量走势'"></ctitle>
          <div class="inner">
            <timeChange class="timeChange" :key="curTypeId+curCity" @change="xflzsTimeClick"></timeChange>
            <LinesR :params="xflzsLineParams"></LinesR>
          </div>
        </div>
        <div class="c-r-c">
          <ctitle :title="'信访量同比、环比走势'"></ctitle>
          <div class="inner">
            <LinesR :params="linesParams"></LinesR>
          </div>
        </div>
        <!--<div class="c-r-b">-->
        <!--<ctitle :title="'信访量万人比走势'"></ctitle>-->
        <!--<div class="inner">-->
        <!--<timeChange class="timeChange" @change="wrbzsTimeClick"></timeChange>-->
        <!--<LinesR :params="wrbzsLineParams"></LinesR>-->
        <!--</div>-->
        <!--</div>-->
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  import typeChange from "@/components/typeChange/index";
  import timeChange from "@/components/timeChange/index";
  import LinesR from "@/components/line/LinesR";
  import Map from "@/components/map/Map";

  export default {
    name: "lmwsts",
    components: {
      ctitle,
      incNumber,
      typeChange,
      timeChange,
      LinesR,
      Map
    },
    data() {
      return {
        curTypeId: 'drop',
        curCity: 'drop',
        //1地图左上角(件次总数)
        jctotal: 0,
        //地图左上角(top3)
        jcTop3: [],
        //2地图
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            JSON: "hebei",
            xName: "name",
            yName: "value",
            map: {
              "value": "信访量"
            },
            carousel: false
          }
        },
        // 3信访量走势
        xflzsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: "rgba(220, 150, 209, 1)",
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0)"
            },
          ],
          option: {
            splitNumber:4,
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "10%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
        //4信访量同比、环比走势
        linesParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          colors: [
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitNumber: 10,
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisYName: "%",
            axisCategoryName: "月",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            left: "5%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "10%"
          },
          map: {},
          xName: "name",
        },
        // 5万人比走势
        wrbzsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: "rgba(220, 150, 209, 1)",
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
      }
    },
    mounted() {
      this.getAPI(this.year, this.curTypeId);
    },
    methods: {
      // 头部类型切换
      changeType(id) {
        this.curTypeId = id;
        this.getAPI(this.year, this.curTypeId);
      },
      handleMapClick(name) {
        this.curCity = name === '全省' ? 'drop' : name;
        this.getOtherAPI(this.year, this.curTypeId);
      },
      getAPI(year, id) {
        // 1件次总数（地图左上角）
        get_http("zlfxWtsdFbTotal", {year: year, xfxs: id})
          .then(res => {
            this.jctotal = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2地图
        get_http("zlfxWtsdFbMap", {year: year, xfxs: id})
          .then(res => {
            this.mapParams.data = res.data.data;
            this.jcTop3 = res.data.data.sort((a, b) => {
              return b.value - a.value
            }).slice(0, 3);
          })
          .catch(err => {
            console.log(err);
          });
        this.getOtherAPI(year, id);
      },
      getOtherAPI(year, id) {
        // 3信访量走势
        this.getXflzsLineAPI(year, id, '年度');
        // 4信访量同比、环比走势
        get_http("zlfxWtsdThbzsLine", {year: 2020, shij_02: this.curCity, xfxs: id})
          .then(res => {
            this.linesParams.map = res.data.map;
            this.linesParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5万人比走势(2020-8-18注释 原因：后台没有数据)
        // this.getWrbzsLineAPI(year, id, '年度');
      },
      // 3信访量走势
      getXflzsLineAPI(year, id, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("zlfxWtsdXflzsLine", {year: _year, xfxs: id, shij_02: this.curCity, timetype: timetype})
          .then(res => {
            this.xflzsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.xflzsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 5万人比走势
      getWrbzsLineAPI(year, id, time) {
        let timetype = '';
        timetype = (time === '年度' ? 'cy' : 'cm');
        get_http("zlfxWtsdWrbzsLine", {year: year, xfxs: id, timetype: timetype})
          .then(res => {
            this.wrbzsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.wrbzsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 信访量走势-时间切换
      xflzsTimeClick(time) {
        this.getXflzsLineAPI(this.year, this.curTypeId, time);
      },
      // 万人比走势-时间切换
      wrbzsTimeClick(time) {
        this.getWrbzsLineAPI(this.year, this.curTypeId, time);
      },
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curTypeId);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .inner
        width 100%
        height calc(100% - 0.28rem)
      .c-left
        position relative
        flex 1
        background url("../../../assets/map-bg.png") no-repeat 130px bottom
        .map-back
          position absolute
          top 0
          right 0
          z-index 1000
          font-size 0.2rem
          color #fff
          cursor pointer
        .map
          position absolute
          top -40px
          left 100px
        .detail
          position absolute
          top 0.6rem
          left 0.6rem
          z-index 1000

          .allNumber
            font-size 0.2rem
            color #5aceff
            margin-bottom 0.2rem
            .number
              font-size 0.5rem
              color #fff
              text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
              margin-right 0.15rem
          .top3
            .label
              float left
              width 0.5rem
              color #ffa800
              font-size 0.18rem
              font-weight 700
              line-height 0.4rem
            .list
              float left
              .item
                line-height 0.4rem
                font-size 0
                color #fff
                .order
                  font-size 0.18rem
                  margin-right 0.1rem
                .name
                  display inline-block
                  width 0.8rem
                  font-size 0.18rem
                .number
                  font-size 0.18rem
                .unit
                  font-size 0.14rem
                  color #5aceff
                  margin-left 0.1rem
      .c-right
        flex 1
        display flex
        flex-direction column
        width 100%
        height 100%
        .timeChange
          top 0
          right .8rem
        .c-r-t
          flex 1
          position relative
        .c-r-c
          flex 1
        .c-r-b
          flex 1
          position relative
</style>
